<template>
  <el-dialog center :visible.sync="dialogState.show" append-to-body :title="title" class="custom_dialog" :custom-class="ScaleSize==='fullscreen'?'':'enlarge'">
    <svg-icon :icon-class="ScaleSize" class="scale-icon" @click="ScaleView" />
    <div class="loading">
      <div v-if="!placeholderFlag&&pathUrl" class="main-content">
        <!-- 预览图片 -->
        <div v-if="fileType==='img'" style="width: 100%; height:100%;text-align:center">
          <el-image :src="pathUrl" class="imgerror" />
        </div>
        <!-- 预览pdf -->
        <embed v-else-if="fileformat==='pdf'" :src="pathUrl" style="width: 100%;height: calc(100% - 5px);">
        <!-- 预览docx文件 -->
        <div v-else-if="fileformat==='docx'" style="text-align:center;height:100%">
          <div ref="docFile" class="docx-file" />
        </div>
        <!-- 预览doc文件 -->
        <div v-else-if="fileformat==='doc'" style="text-align:center;height:100%">
          <embed id="pdf-box" :src="'https://view.officeapps.live.com/op/view.aspx?src='+pathUrl" style="width: 100%;height: calc(100% + 50px);position: absolute;top: -54px;    left: 0;">
        </div>
        <!-- 无法预览的文件 -->
        <div v-else style="text-align:center">
          <el-image v-if="placeholderIcon!==''" :src="placeholderIcon" style="width:420px;height: 420px;" />
          <div style="text-align:center;color: red;font-size: 14px;">
            暂不支持预览文件格式为:{{ fileformat }}的文件,
            <el-link :underline="false" type="primary" style="font-size: 16px;" @click="downloadByBlob(dialogState.url)">点击下载</el-link>
          </div>
        </div>
      </div>
      <div v-else style="width: 100%; height:100%;display: flex;justify-content: center;align-items: center;">
        <el-image v-if="placeholderIcon!==''" :src="placeholderIcon" style="width:420px;height: 420px;" />
      </div>
    </div>
  </el-dialog>
</template>

<script>
// cnpm i docx-preview --save  安装  参考https://zhuanlan.zhihu.com/p/437059185
import axios from "axios";
const docx = require("docx-preview");
export default {
  props: {
    title: {
      type: String,
      default: function() {
        return "";
      }
    },
    dialogState: {
      type: Object,
      default: function() {
        return { show: false };
      }
    }
  },
  data() {
    return {
      ScaleSize: "fullscreen", // 预览弹窗缩放
      loading: true,
      pathUrl: '',
      placeholderIcon: '',
      placeholderFlag: false,
      fileType: "",
      fileformat: "",
      changeFlag: 0
    }
  },
  watch: {
    dialogState: { // 当数据发生改变时，会自动去计算
      handler(newValue, oldValue) {
        if (!this.dialogState.url) {
          this.dialogState.show = false
          this.$message.error('预览地址不存在')
          return
        }
        setTimeout(() => {
          this.loading.close();
        }, 1000);
        this.placeholderIcon = ''
        this.placeholderFlag = false
        this.pathUrl = ''
        this.openFullScreen2()
        this.getData(this.dialogState.url)
      }
    },
    deep: true,
    immediate: true // 关键
  },
  mounted() {
  },
  methods: {
    // 接收文件流转换地址再显示
    getData(url) {
      this.changeFlag = 0
      this.fileformat = url.split('?')[0].substring((url.lastIndexOf('.') + 1))
      // this.fileformat = url.substring((url.lastIndexOf('.') + 1))
      if (!url) {
        this.loading.close();
        this.placeholderFlag = true
        this.placeholderIcon = require("./img/nofiles.png")
        return
      }
      axios.get(url, {
        url: url,
        method: 'post',
        headers: { 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' },
        responseType: 'blob',
      })
        .then(res => {
          console.log(res)
          const files = ['pdf', 'docx', 'doc']
          // 图片后缀名
          const type = ['png', 'jpg', 'gif', 'jpeg', 'webp', 'bmp', 'tif', 'pcx', 'tga', 'exif', 'fpx', 'svg', 'psd', 'cdr', 'pcd', 'dxf', 'ufo', 'eps', 'ai', 'raw', 'WMF']
          this.fileType = type.some(value => value === this.fileformat) ? 'img' : files.some(value => value === this.fileformat) ? '' : 'other'
          if (res.data.size === 0) {
            this.loading.close();
            this.placeholderFlag = true
            this.placeholderIcon = require("./img/InvalidFile.png")
            return
          }
          if (this.fileType === 'other') {
            this.placeholderIcon = require("./img/nofiles.png")
            this.loading.close();
            this.pathUrl = url
            return
          }
          if (this.fileformat === 'doc') { // 使用插件无法展示  需要使用wps的https://view.officeapps.live.com/op/view.aspx
            this.loading.close();
            this.pathUrl = url
            return
          }
          if (this.fileformat === 'docx') {
            this.loading.close();
            axios({
              method: "get",
              responseType: "blob",
              url: url,
            }).then(({ data }) => {
              docx.renderAsync(data, this.$refs.docFile);
            });
            this.pathUrl = url
            return
          }
          const blob = new Blob([res.data], { type: res.data.type })
          this.pathUrl = URL.createObjectURL(blob)
          this.loading.close();
        })
        .catch(err => { // 图片请求失败 展示文件失效默认图片
          this.loading.close();
          this.placeholderFlag = true
          this.placeholderIcon = require("./img/InvalidFile.png")
          console.log(err, 1111)
        })
    },
    openFullScreen2() {
      this.loading = this.$loading({
        lock: true,
        text: "加载中",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)",
        target: document.querySelector(".loading") // 指定区域
      });
    },
    downloadByBlob(url, name) { // 前端通过地址下载图片
      if (this.changeFlag === 0) {
        this.changeFlag = 1;
        setTimeout(() => {
          this.changeFlag = 0
        }, 3000);
      } else {
        this.$message.error('请勿频繁操作！');
        return
      }
      axios.get(url, {
        url: url,
        method: 'post',
        headers: { 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' },
        responseType: 'blob',
      })
        .then(res => {
          const blob = new Blob([res.data], { type: res.data.type })
          this.pathUrl = URL.createObjectURL(blob)
          window.open(this.pathUrl)
          this.loading.close();
        })
        .catch(err => {
          console.log(err)
        })
    },
    ScaleView() {
      this.ScaleSize = this.ScaleSize === 'exit-fullscreen' ? 'fullscreen' : 'exit-fullscreen'
    },

  }

}
</script>
<style lang="scss" scoped>
// 自定义头部弹窗
.custom_dialog {
  ::v-deep .el-dialog {
    border-radius: 5px;
    margin-top: 10vh !important;
    height: 80vh;
    .el-dialog__header {
      position: relative;
      z-index: 9;
      background: #dde4e9;
      padding: 15px 20px;
      height: 54px;
      .el-dialog__title {
        font-weight: 600;
      }
      .el-dialog__headerbtn {
        font-size: 25px;
        position: absolute;
        top: 15px;
        right: 20px;
      }
    }
    .el-dialog__body {
      position: relative;
      font-size: 18px;
      height: 100%;
      padding: 0;
    }
  }
}

.imgerror {
  ::v-deep .el-image__error {
    height: 200px;
    width: 200px;
    font-size: 20px;
  }
}
.loading {
  width: 100%;
  height: calc(100% - 54px);
}
.main-content {
  overflow: auto;
  height: 100%;
}
.custom_dialog {
  ::v-deep.enlarge.el-dialog {
    width: 100%;
    height: 100vh;
    margin-top: 0vh !important;
    .el-dialog__body {
      padding: 0;
      height: calc(100% - 55px);
      .loading {
        height: 100%;
      }
    }
  }
}

.scale-icon {
  position: absolute;
  right: 75px;
  top: -35px;
  z-index: 99;
}
.docx-file {
  ::v-deep.docx-wrapper {
    padding: 0;
    .docx {
      padding: 0 !important;
      width: 100% !important;
      margin: 0 !important;
      min-height: auto !important;
      box-shadow: none !important;
    }
  }
}
</style>
